import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SheetElement, SheetElementValue } from 'src/app/classes/sheet-element';

@Component({
  selector: 'app-create-character',
  templateUrl: './create-character.component.html',
  styleUrls: ['./create-character.component.scss']
})
export class CreateCharacterComponent implements OnInit {

  @Input() races!: Array<SheetElement>;
  @Input() classes!: Array<SheetElement>;
  @Input() abilities!: Array<SheetElement>;
  @Input() skills!: Array<SheetElement>;
  @Input() bundles!: Array<SheetElement>;
  @Input() stepCreation!: number;
  @Output() stepChanged = new EventEmitter();
  public points: number = 3000;
  public error!: string;
  public scheda: { [key: string]: any; } = {};
  constructor() { }

  ngOnInit(): void {
    this.error = "";
    this.scheda["abilities"] = new Array<SheetElementValue>();
    this.scheda["skills"] = new Array<SheetElementValue>();
    this.scheda["bundles"] = new Array<SheetElementValue>();
  }

  AbilitiesChanged(r: Array<SheetElementValue>) {
    this.scheda["abilities"] = new Array<SheetElementValue>();
    r.forEach((a: SheetElementValue) => {
      this.scheda["abilities"].push(a);
    });
    this.CalculatePoints();
  }
  SkillsChanged(r: Array<SheetElementValue>) {
    this.scheda["skills"] = new Array<SheetElementValue>();
    r.forEach((a: SheetElementValue) => {
      this.scheda["skills"].push(a)
    });
    this.CalculatePoints();
  }
  BundlesChanged(r: Array<SheetElementValue>) {
    this.scheda["bundles"] = new Array<SheetElementValue>();
    r.forEach((a: SheetElementValue) => {
      this.scheda["bundles"].push(a)
    });
    this.CalculatePoints();
  }
  CalculatePoints() {
    this.error = "";
    this.points = 3500;

    this.scheda["abilities"].forEach((a: SheetElementValue) => {
      if((a.Value < a.Element.Min) || (a.Value > a.Element.Max)) {
        this.error += a.Element.Name + " deve essere compreso tra " + a.Element.Min.toString() + " e " + a.Element.Max.toString() + ", ";
      }
      this.points -= a.Value * a.Value * a.Element.Cost;
    });
    this.scheda["skills"].forEach((a: SheetElementValue) => {
      if((a.Value < a.Element.Min) || (a.Value > a.Element.Max)) {
        this.error += a.Element.Name + " deve essere compreso tra " + a.Element.Min.toString() + " e " + a.Element.Max.toString() + ", ";
      }
      this.points -= a.Value * a.Value * a.Element.Cost;
    });
    this.scheda["bundles"].forEach((a: SheetElementValue) => {
      if(a.Value) {
        this.points -= a.Element.Cost;
      }
    });
    if(this.points < 0) {
      this.error += "Troppi punti spesi!, ";
    }
  }
  Back() {
    this.stepCreation--;
    this.stepChanged.emit(this.stepCreation);
  }
  Select(r: SheetElement) {
    this.scheda[r.Name] = r;
    this.Next();
  }
  Next() {
    this.stepCreation++;
    this.stepChanged.emit(this.stepCreation);
  }
  CancelCreation() {
    this.stepCreation = -1;
    this.stepChanged.emit(this.stepCreation);
  }
}
