import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SheetElement, SheetElementValue } from 'src/app/classes/sheet-element';

@Component({
  selector: 'app-edit-skills',
  templateUrl: './edit-skills.component.html',
  styleUrls: ['./edit-skills.component.scss']
})
export class EditSkillsComponent implements OnInit {

  @Input() skills!: Array<SheetElement>;
  @Output() valueChanged = new EventEmitter();
  public frmGroup!: FormGroup;
  @Output() next = new EventEmitter();
  constructor(private fb: FormBuilder) { }

  ngOnInit(): void {
    this.frmGroup = this.createSignupForm();
  }
  createSignupForm(): FormGroup {
    let vett: { [key: string]: any; } = {};
    this.skills.forEach((a: SheetElement) => {
      vett[a.ID.toString()] = [ a.Min, Validators.compose([Validators.required, Validators.min(a.Min), Validators.max(a.Max)]) ];
    });
    return this.fb.group(vett);
  }
  ValueChanged() {
    let values = new Array<SheetElementValue>();
    this.skills.forEach((a: SheetElement) => {
      let value = new SheetElementValue()
      value.Element = a;
      value.Value = this.frmGroup.controls[a.ID.toString()].value;
      values.push(value);
    });
    this.valueChanged.emit(values);
  }
  onSubmit() {
    this.ValueChanged();
    this.next.emit();
  }
}
