import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { User } from 'src/app/classes/user';
import { UserType } from 'src/app/classes/user-type';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.scss']
})
export class EditUserComponent implements OnInit {

  public frmGroup!: FormGroup;
  @Input() selectedUsertype!: User;
  @Input() error!: string;
  @Input() userTypes!: Array<UserType>;
  @Output() saved = new EventEmitter();

  constructor(private fb: FormBuilder) { }

  ngOnInit(): void {
    this.frmGroup = this.createSignupForm();
  }

  createSignupForm(): FormGroup {
    let vett: { [key: string]: any; } = {};
    if(this.selectedUsertype == null) {
      vett['usertype'] = [ "", Validators.compose([Validators.required]) ];
    }
    else {
      vett['usertype'] = [ this.selectedUsertype.UserType.ID, Validators.compose([Validators.required]) ];
    }
    var resullt = this.fb.group(vett);
    return resullt;
  }

  onSubmit() {
    this.selectedUsertype.IDUserType = parseInt(this.frmGroup.controls['usertype'].value);
    this.saved.emit(this.selectedUsertype);
  }

}
