import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { UserType } from 'src/app/classes/user-type';

@Component({
  selector: 'app-edit-user-type',
  templateUrl: './edit-user-type.component.html',
  styleUrls: ['./edit-user-type.component.scss']
})
export class EditUserTypeComponent implements OnInit {

  public frmGroup!: FormGroup;
  @Input() selectedUsertype!: string;
  @Input() error!: string;
  @Output() saved = new EventEmitter();

  constructor(private fb: FormBuilder) { }

  ngOnInit(): void {
    this.frmGroup = this.createSignupForm();
  }

  createSignupForm(): FormGroup {
    let vett: { [key: string]: any; } = {};
    if(this.selectedUsertype == null) {
      vett['usertype'] = [ "", Validators.compose([Validators.required]) ];
    }
    else {
      vett['usertype'] = [ this.selectedUsertype, Validators.compose([Validators.required]) ];
    }
    var resullt = this.fb.group(vett);
    return resullt;
  }

  onSubmit() {
    let u = new UserType();
    u.Description = this.frmGroup.controls['usertype'].value;
    this.saved.emit(u);
  }

}
