import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { SheetElement, SheetElementValue } from 'src/app/classes/sheet-element';

@Component({
  selector: 'app-edit-bundles',
  templateUrl: './edit-bundles.component.html',
  styleUrls: ['./edit-bundles.component.scss']
})
export class EditBundlesComponent implements OnInit {

  @Input() bundles!: Array<SheetElement>;
  @Output() valueChanged = new EventEmitter();
  @Output() next = new EventEmitter();
  public frmGroup!: FormGroup;
  constructor(private fb: FormBuilder) { }

  ngOnInit(): void {
    this.frmGroup = this.createSignupForm();
  }
  createSignupForm(): FormGroup {
    let vett: { [key: string]: any; } = {};
    this.bundles.forEach((a: SheetElement) => {
      vett[a.ID.toString()] = [ a.Min ];
    });
    return this.fb.group(vett);
  }

  ValueChanged() {
    let values = new Array<SheetElementValue>();
    this.bundles.forEach((a: SheetElement) => {
      let value = new SheetElementValue();
      value.Element = a;
      value.Value = this.frmGroup.controls[a.ID.toString()].value;
      values.push(value);
    });
    this.valueChanged.emit(values);
  }
  onSubmit() {
    this.ValueChanged();
    this.next.emit();
  }
}
